import React, { useState } from "react";
import ToDoList from "./ToDoList";

const App = () => {

    const [list, setList] = useState("");
    const [item, setItem] = useState([])

    const inputEvent = (e) => {
        console.log(e.target.value)
        setList(e.target.value)
    }
    const getItems = () => {
        setItem((oldItem) => {
            return [...oldItem, list]
        })
        setList("")
    }

    const DelItem = (id) => {
        setItem((oldItems) => {
            return oldItems.filter((arrElm, index) => {
                return index !== id;
            })
        })
    }

    return (
        <>
            <div class="modal-dialog py-5" role="document">
                <div class="modal-content rounded-4 shadow">
                    <div class="modal-body p-4 text-center ">
                        <h1>ToDo List</h1>
                        <form class="d-flex">
                            <input class="form-control me-2" type="text" placeholder="Add a Items" aria-label="Search" onChange={inputEvent} name={list} value={list} />
                            <button class="btn btn-outline-success" type="button" onClick={getItems}>Add</button>


                        </form>
                        <hr />
                        <ul class="list-group ">
                            <h4 className="text-center">This are List</h4>

                            {item.map((itemValue, index) => {
                                return <ToDoList
                                    id={index}
                                    key={index}
                                    text={itemValue}
                                    setValue={DelItem}
                                />
                            })
                            }
                        </ul>

                    </div>
                </div>
            </div>

        </>
    )
}
export default App

